.SearchBar {
    top: 0;
    left: 0;
    position: sticky;
    z-index: 1100;
    display: flex;

    justify-content: center;
    align-items: center;
    min-height: 64px;
    padding-right: 24px;
    padding-left: 24px;
    padding-top: 12px;
    padding-bottom: 12px;
    color: #fff;
    background-color: #3f51b5;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
        0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.SearchForm {
    display: flex;

    align-items: center;
    width: 100%;
    height: 100%;
    max-width: 600px;
    background-color: #fff;
    border-radius: 3px;
    padding: 0;

}

.SearchForm_button {
    display: inline-block;
    width: 35px;
    height: 35px;
    border: 0;
    background-color: #3f51b5;
    align-content: center;
    font-size: 25px;
    height: 45px;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
}

.SearchForm_button:hover {
    background-color: #3f51b5;
    opacity: 0.8;
}

